* {
  margin-block: 0 0;
  font-family: "Right Grotesk","Helvetica";
}

@font-face {
    font-family: 'Right Grotesk';
    src: url(./assets/fonts/PPRightGrotesk-Medium.otf) format('opentype');
    font-weight: 500;
}
@font-face {
    font-family: 'Right Grotesk';
    src: url(./assets/fonts/PPRightGrotesk-Bold.otf) format('opentype');
    /* other formats include: 'woff2', 'truetype, 'opentype',
                              'embedded-opentype', and 'svg' */
    font-weight: 900;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: url(./assets/imgs/shopping-cart.jpg);
  background-size: 150px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.App {
  overflow: hidden;
  height: 100%;
}

.Title {
  /* height: 80vh; */
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: column;
  /* min-height: 45vh; */
  max-height: 80vh;
  background-color: #00F;
  /* overflow: hidden; */

}

.Title-Logo {
  width: 100%;
  height: 100%;
  max-width: 400px;
  max-height: 50vh;

}

.Scroller-Cont {
  overflow: hidden;
  width: 100%;
}

.Scroller {
  width: 100%;
  background-color: #00F;
  border: 1px solid white;
  border-width: 1px 0;
}

@keyframes scroller {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(-300%);
  }
}

@keyframes scrollerreverse {
  0% {
    transform: translateX(-300%);
  }
  100% {
    transform: translateX(0);
  }
}



.Scroller > div {
  width: 4000px;
  animation-duration: 100s;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
  /* padding: 6px; */
  display: flex;
  align-items: center;

}

.Scroller > div > * {
  display: inline-block;
  margin: 0 12px;

}

.normal {
  animation-name: scroller;
}

.reverse {
  animation-name: scrollerreverse;
}

.Scroller img {
    width: 60px;
}

.Scroller > div > span {
  text-transform: uppercase;
  font-stretch: 50%;
  font-size: 22px;
  color: white;
  text-wrap: nowrap;
  font-family: "Right Grotesk", "Helvetica";
  display: inline-block;
  transform: scale(0.8,1);
  margin: 0 -12px;
}

.Scroller > div > svg {
  height: 48px;
  min-width: 48px
}

@media only screen and (max-width: 600px) { 

    .Scroller > div {
        padding: 4px;
    }
    .Scroller > div > span {
        text-transform: uppercase;
        font-stretch: 70%;
        font-size: 22px;
        color: white;
        text-wrap: nowrap;
      }

      .Scroller img {
        width: 60px
      }
}

.PatternLine {
  width: 100%;
  padding: 12px;
  height: 50px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.PatternLine > div {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.Products {
  /* width: 100%; */
