.box {
    padding: 24px 18px;
    /* box-sizing: border-box; */
}

.nopadding {
    padding: 0;
}
.tightbox {
    padding: 8px 8px;
}

.blue {
    background-color: #00F;
    /* border: 1px solid white; */
    border-width: 1px 0;
    color: white;
}

