    background-color: #FFF;
    color: black;
    /* border: 2px solid black; */

}

.ItemBox-Wrapper {

}

.ItemBox {
    width: 50%;
    max-width: 350px;
    outline: 2px solid black;
    background-color: white;
    position: relative;
    /* max-height: 50vh; */
}

.arrows {
    font-size: 24px;
    text-align: center;
}

.arrows div {
    width: 50%;
    background-color: white;
    color: black;
    cursor: pointer
}

.ItemBox a {
    display: block;
    width: 100%;
    text-align: center;
    padding: 12px;
    font-size: 24px;
    border: none;
    border-top: 1px solid black;
    background-color: black;
    color: white;
    box-sizing: border-box;
    cursor: pointer;
    text-decoration: none;
}

.ItemBox a:hover {
    background-color: blue;
    cursor: pointer;
}

.Sticker {
    position: absolute;
    background-color: red;
    color: white;
    font-weight: 900;
    padding: 12px;
    top: 0px;
    border-radius: 1000px;
    width: 52px;
    height: 52px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 32px;

}

.middlespacer {
    display: none
}

@media only screen and (max-width: 600px) { 
    h3 {
        font-size: 28px;
    }
    
    .ItemBox {
        width: 100%;
        max-width: 500px;
    }

    .ItemBox-Title {
    }

    .arrows {
        font-size: 36px;
    }

    .middlespacer{
        width: 100%;
        background-color: white;
        display: block
    }
}